.dashboard-container2 {
	width: 100%;
	height: 350px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: #000;
	background-image: linear-gradient(
			180deg,
			rgba(0, 0, 0, 0.75),
			rgba(0, 0, 0, 0.75)
		),
		url(../../img/fullHD/setup_1920.jpg);
	background-position: center center;
	background-size: cover;
	position: fixed;
	bottom: 0;
	z-index: 400;
	transform: translateY(100%);
	transition: all 0.5s ease-in;
	border-top: 10px solid #000;
}

.showing {
	transform: translateY(0%);
}

.dashboard2 {
	width: 100%;
	margin: 0 auto;
}

.glareshield2 {
	width: 150px;
	height: 50px;
	position: absolute;
	left: 50%;
	transform: translate(-50%);
	bottom: 100%;
	background-color: rgba(0, 0, 0, 0.7);
	cursor: pointer;
	color: #fff;
	display: flex;
	justify-content: center;
	align-items: center;
	border: 2px solid #fff;
	border-bottom: none;
	border-radius: 2px;
	font-family: 'Bebas';
	font-size: 1.5rem;
	letter-spacing: 0.3rem;
	transform: translateY(450px);
	transition: all 0.5s ease-in;
	z-index: 0;
}

.glareshield-showing {
	transform: translateY(0%);
}

.dashboard-data2 {
	height: 350px;
	display: flex;
	flex-direction: row;
	align-items: flex-start;
	justify-content: center;
	color: #fff;
}

.general-data2 {
	height: 350px;
	width: 95%;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	align-items: center;
	justify-content: center;
	overflow: scroll;
}

.first-row-airport {
	margin-top: 400px;
	display: flex;
	gap: 0.5rem;
	margin-bottom: 1rem;
}

.second-row-airport {
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
	margin-bottom: 1rem;
}

.current-weather {
	font-family: 'Bebas';
}

.current-airport-info {
	width: 50%;
	height: 420px;
	align-self: flex-start;
	display: flex;
	flex-direction: column;
	padding: 1rem;
	border-radius: 3px;
	background-color: rgba(0, 0, 0, 0.7);
	box-shadow: 2px 2px 5px #000;
}

.current-airport-weather {
	width: 50%;
	height: 420px;
	align-self: flex-start;
	padding: 1rem;
	border-radius: 3px;
	background-color: rgba(0, 0, 0, 0.7);
	box-shadow: 2px 2px 5px #000;
}

.current-details {
	font-family: 'Bebas';
	font-size: 1rem;
}

.current-details-headline {
	font-family: 'Bebas';
	font-size: 1.5rem;
	text-decoration: underline;
}

.loading-spinner {
	align-self: flex-start;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 350px;
	overflow: auto;
	padding: 1rem;
	border-radius: 3px;
	background-color: rgba(0, 0, 0, 0.7);
	box-shadow: 2px 2px 5px #000;
}

.arrival-history {
	width: 100%;
	height: 350px;
	overflow: auto;
	padding: 1rem;
	border-radius: 3px;
	background-color: rgba(0, 0, 0, 0.7);
	box-shadow: 2px 2px 5px #000;
	text-align: center;
}

.arrivals {
	display: flex;
	flex-direction: row;
	padding: 0.3rem;
	gap: 1rem;
}

.departure-history {
	width: 100%;
	align-self: flex-start;
	height: 350px;
	overflow: auto;
	padding: 1rem;
	border-radius: 3px;
	background-color: rgba(0, 0, 0, 0.7);
	box-shadow: 2px 2px 5px #000;
	text-align: center;
}

.departures {
	display: flex;
	flex-direction: row;
	padding: 0.3rem;
	gap: 1rem;
}

.history-button {
	padding: 1rem;
	gap: 1rem;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 50px;
	width: 100%;
	cursor: pointer;
	color: #fff;
	border-radius: 2px;
	border: 1px solid #fff;
	font-family: 'Bebas';
	font-size: 1.5rem;
	background-color: rgba(0, 0, 0, 0.7);
	box-shadow: 2px 2px 5px #000;
}

